
input,select,textarea {
  outline: none;
}
input{
  box-sizing: border-box;
  font-size: 14px;
  padding: 2px;
}
.title-input{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-right: 20px;
}
.url-input{
  -webkit-box-flex: 3;
      -ms-flex: 3;
          flex: 3;
}
.form-item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 24px;
}
a {
  display: inline-block;
  cursor: pointer;
  width: 20px;
  text-align: center;
  color: #0baaf7;
  text-decoration: none;
}
.form-item{
  margin-bottom: 10px;
}
.form-item:last-child{
  margin-bottom: 30px;
}
#app {
  margin: 10px auto;
  width: 960px;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: left;
  color: #2c3e50;
  margin-top: 60px;
  font-size: 18px;
}
.javascript-editor {
  height: 500px;
}




